<template>
    <div>
        <div class="container">
            <div>
                <ul class="nav justify-content-around py-3 text-center">
                    <li class="nav-item p-3 bg-desert text-light rounded-pill w-25">填寫訂單資料</li>
                    <li class="nav-item p-3 text-secondary rounded-pill w-25">確認資料及付款</li>
                    <li class="nav-item p-3 text-secondary rounded-pill w-25">完成</li>
                </ul>
            </div>
            <div class="row justify-content-center mt-4">
                <div class="col-lg-8">
                    <div class="container mb-3">
                        <div class="accordion" id="accordionExample">
                            <div class="card">
                                <div class="card-header d-flex " id="headingOne">
                                    <h2 class="mb-0">
                                        <button class="btn btn-link" type="button" data-toggle="collapse" data-target="#collapseOne" aria-expanded="true" aria-controls="collapseOne">
                                        查看訂單
                                        </button>
                                    </h2>
                                    <h3 class="text-right ml-auto">
                                        {{carts.final_total | currency}}
                                    </h3>
                                </div>
                                <div id="collapseOne" class="collapse" aria-labelledby="headingOne" data-parent="#accordionExample">
                                    <div class="card-body">
                                        <table class="table">
                                            <tbody>
                                                <tr v-for="item in carts.carts" :key="item.id">
                                                    <td width="50">
                                                        <a href="javascript:;" @click.prevent="removeCart(item.id)">
                                                            <i class="far fa-trash-alt text-danger"></i>
                                                        </a>
                                                    </td>
                                                    <td>{{item.product.title}}</td>
                                                    <td>{{item.qty}}/{{item.product.unit}}</td>
                                                    <td class="text-right">{{item.final_total | currency}} 元</td>
                                                </tr>
                                                <tr>
                                                    <td  colspan="4" class="text-right" v-if="carts.total==carts.final_total">
                                                        售價 <strong>{{carts.total | currency}} 元</strong>
                                                    </td>
                                                </tr>
                                                <tr>
                                                    <td  colspan="4" class="text-right text-danger" v-if="carts.total!=carts.final_total">
                                                        優惠特價 <strong>{{carts.final_total | currency}} 元</strong>
                                                    </td>
                                                </tr>
                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="input-group mb-3 input-group-sm">
                            <input type="text" class="form-control" v-model="coupon_code" placeholder="請輸入優惠碼">
                            <div class="input-group-append">
                                <button @click.prevent="addCouponCode" class="btn btn-outline-secondary" type="button">
                                套用優惠碼
                                </button>
                            </div>
                        </div>
                        
                        <div class="my-5 row justify-content-center">
                            <form class="col-md-10" @submit.prevent="ckeckoutOrder">
                                <div class="bg-light py-3 h4 text-center">訂購人資訊</div>
                                <div class="form-group">
                                    <label for="useremail" >Email</label>
                                    <input type="email" class="form-control" name="email" id="useremail"
                                    v-validate="'required|email'" v-model="form.user.email" placeholder="請輸入 Email">
                                    <span class="text-danger" v-if="errors.has('email')">{{ errors.first('email') }}</span>
                                </div>
                            
                                <div class="form-group">
                                    <label for="username">收件人姓名</label>
                                    <input type="text" class="form-control" name="name" id="username"
                                    v-validate="'required'" :class="{'is-invalid':errors.has('name')}" v-model="form.user.name" placeholder="輸入姓名">
                                    <span class="text-danger" v-if="errors.has('name')">請輸入姓名欄位</span>
                                </div>
                            
                                <div class="form-group">
                                    <label for="usertel">收件人電話</label>
                                    <input type="tel" class="form-control" name="tel" id="usertel" 
                                    v-validate="'required'" :class="{'is-invalid':errors.has('tel')}" v-model="form.user.tel" placeholder="請輸入電話">
                                    <span class="text-danger" v-if="errors.has('tel')">請輸入電話欄位</span>
                                </div>
                            
                                <div class="form-group">
                                    <label for="useraddress">收件人地址</label>
                                    <input type="text" class="form-control" name="address" id="useraddress" v-model="form.user.address"
                                    v-validate="'required'" :class="{'is-invalid':errors.has('address')}" placeholder="請輸入地址">
                                    <span class="text-danger" v-if="errors.has('address')">請輸入地址欄位</span>
                                </div>
                            
                                <div class="form-group">
                                    <label for="comment">留言</label>
                                    <textarea name="" id="comment" class="form-control" cols="30" rows="10" v-model="form.message"></textarea>
                                </div>
                                <div class="text-right">
                                    <router-link to="/air_products" class="btn btn-outline-desert">
                                    <i class="fas fa-chevron-circle-left mr-2"></i>繼續購物</router-link>
                                    <button class="btn btn-outline-primary ml-2">
                                        <i class="far fa-paper-plane mr-2"></i>送出訂單</button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data(){
        return{
            carts: {},
            coupon_code: '',
            form:{
                user: {
                    name : '',
                    email : '',
                    tel : '',
                    address : '',
                },
                message: '',
            }
        }
    },
    methods:{
        getCart(){
            const vm = this;
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart`
            vm.$http.get(api).then((response) => {
                console.log(response.data)
                vm.carts = response.data.data;
            })
        },
        removeCart(id){
            const vm = this;
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/cart/${id}`
            vm.$http.delete(api).then((response) => {
                if(response.data.success){
                    vm.getCart();
                }
            })
        },
        addCouponCode(){
            const vm = this;
            const coupon = {
                code : vm.coupon_code,
            };
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/coupon`
            vm.$http.post(api,{data:coupon}).then((response) => {
                console.log(response.data)
                if(response.data.success){
                    vm.getCart();
                }
            })
        },
        ckeckoutOrder(){
            const vm = this;
            const userdata = vm.form;
            const api = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`
            this.$validator.validate().then((valid) => {
                if (valid) {
                    vm.$http.post(api,{data:userdata}).then((response) => {
                        console.log(response.data)
                        if(response.data.success){
                            vm.$router.push(`/checkout_order/${response.data.orderId}`)
                        }
                    })
                }else{
                    console.log('欄位不完整');
                }
            });
            
        },
        
    },
    created(){
        this.getCart();
    }

}
</script>


