import Vue from 'vue'
import VueRouter from 'vue-router'

import Login from '@/components/Login'

import Home from '@/components/web_main/Home'
import Index from '@/components/web_main/Index'
import AirProducts from '@/components/web_main/AirProducts'
import CreateOrder from '@/components/web_main/CreateOrder'
import CheckOrder from '@/components/web_main/CheckOrder'

import Dashboard from '@/components/Dashboard'
import Products from '@/components/pages/Products'
import Orders from '@/components/pages/Orders'
import Coupons from '@/components/pages/Coupons'

Vue.use(VueRouter)

export default new VueRouter({
    linkActiveClass: 'active', //切記加入 才會有 active 的樣式
    routes:[
        {
            path:'*',
            redirect:'/'
        },
        {
            path :'/',
            component : Home,
            children:[
                {   
                    name :'Index', //元件名稱
                    path :'/',
                    component : Index,
                },
                {   
                    name :'AirProducts', //元件名稱
                    path :'/air_products',
                    component : AirProducts,
                },
                {   
                    name :'CreateOrder', //元件名稱
                    path :'/create_order',
                    component : CreateOrder,
                },
                {   
                    name :'CheckOrder', //元件名稱
                    path :'/checkout_order/:orderId',
                    component : CheckOrder,
                },
            ]
        },
        {   
            name :'Login', //元件名稱
            path :'/login',
            component : Login,
        },
        {   
            name :'Dashboard', //元件名稱
            path :'/admin',
            component : Dashboard,
            children:[
                {   
                    name :'Products', //元件名稱
                    path :'products',
                    component : Products,
                    meta: {requiresAuth: true},
                },
                {   
                    name :'Orders', //元件名稱
                    path :'orders',
                    component : Orders,
                    meta: {requiresAuth: true},
                },
                {   
                    name :'Coupons', //元件名稱
                    path :'coupons',
                    component : Coupons,
                    meta: {requiresAuth: true},
                },
            ],
        }
    ],
})




